"use client";
import React, { useState } from "react";
import Image from "next/image";

export default function Navbar() {
  const [activeNav, setActiveNav] = useState("Homepage");

  return (
    <nav className="flex items-center justify-between px-8 lg:px-16 py-6 backdrop-blur-md bg-white/5 border-b border-white/10 sticky top-0 z-50">
      <div className="flex items-center gap-2 group cursor-pointer">
        <div className="p-2 bg-white rounded-lg shadow-lg shadow-blue-500/30 group-hover:scale-110 transition-transform">
          <Image src="/logo.png" width={120} height={500} alt="logo" />
        </div>
      </div>

      <div className="hidden md:flex items-center gap-8">
        {["Homepage", "Destinations", "About", "Support"].map((item) => (
          <button
            key={item}
            onClick={() => setActiveNav(item)}
            className={`text-sm font-medium transition-all duration-300 relative py-1 ${
              activeNav === item
                ? "text-white"
                : "text-white/60 hover:text-white"
            }`}
          >
            {item}
            {activeNav === item && (
              <span className="absolute bottom-0 left-0 w-full h-0.5 bg-blue-400 rounded-full" />
            )}
          </button>
        ))}
      </div>

      <div className="flex items-center gap-4">
        <button className="px-6 py-2.5 bg-white/10 hover:bg-white/20 border border-white/20 rounded-full text-white text-sm font-semibold backdrop-blur-md transition-all">
          Sign In
        </button>
        <button className="px-6 py-2.5 bg-blue-600 hover:bg-blue-500 text-white text-sm font-semibold rounded-full shadow-lg shadow-blue-600/20 transition-all">
          Register
        </button>
      </div>
    </nav>
  );
}
